import type { Menu } from 'lib/shopify/types';

/**
 * Демо-меню для навигации (header и footer)
 */

export const DEMO_HEADER_MENU: Menu[] = [
  {
    title: 'All',
    path: '/search'
  },
  {
    title: 'Products',
    path: '/search'
  },
  {
    title: 'About',
    path: '/about'
  }
];

export const DEMO_FOOTER_MENU: Menu[] = [
  {
    title: 'About',
    path: '/about'
  },
  {
    title: 'Terms',
    path: '/terms'
  },
  {
    title: 'Shipping',
    path: '/shipping'
  },
  {
    title: 'Privacy',
    path: '/privacy'
  },
  {
    title: 'Returns',
    path: '/returns'
  },
  {
    title: 'FAQ',
    path: '/faq'
  }
];

/**
 * Получить демо-меню по handle
 */
export function getDemoMenu(handle: string): Menu[] {
  if (handle === 'next-js-frontend-header-menu') {
    return DEMO_HEADER_MENU;
  }
  if (handle === 'next-js-frontend-footer-menu') {
    return DEMO_FOOTER_MENU;
  }
  return [];
}

